import counter from "../../../components/counter.vue"
export default {
	components: {
		counter,
	},
	data() {
		return {
			footerbottom: 0,
			cartList: [],
			theIndex: null, // 控制滑动效果 当前滑动下标
			oldIndex: null, // 上一个左滑下标
			selectedList: [],
			isAllselected: false,
			sumPrice: 0.00
		}
	},
	onShow() {
		// 获取本地缓存
		uni.getStorage({
			key: "cartList",
			success: (res => {
				// console.log(res)
				for (let i = 0; i < res.data.length; i++) {
					res.data[i].selected = false;
				}
				this.cartList = res.data;

				// 属性数据初始化
				this.selectedList = [];
				this.isAllselected = false;
				this.sumPrice = "0.00";
			})
		})
	},
	onLoad() {
		// 兼容H5底部菜单



	},
	methods: {
		add(item) {
			item.number++;
			this.sum()
		},
		sub(item) {
			if (item.number <= 1) {
				return
			}
			item.number--;
			this.sum()
		},
		// 结算
		account() {
			// 需要判断
			if (this.selectedList < 1) { //没有商品
				uni.showToast({
					title: "还未选择购买商品",
					icon: "none"
				})
				return;
			}
			// 有商品 先进行本地存储
			uni.setStorage({
				key: "accountList",
				data: this.selectedList,
				success: () => { //跳转结算页
					uni.navigateTo({
						url: "../../goods/goods-account"
					})
				}
			})



		},
		// 全删
		handleDeleteAll() {
			for (let i = 0; i < this.selectedList.length; i++) {
				let item = this.selectedList[i]
				this.handleDelete(item)
			}

			this.selectedList = [];
			this.isAllselected = false;
			this.sum();
		},
		// 左滑删除
		handleDelete(item) {
			uni.getStorage({
				key: "cartList",
				success: (res) => {
					res.data.splice(res.data.indexOf(item, 1));
					uni.setStorageSync("cartList", res.data);
				}
			})

			// 更新数组
			this.cartList.splice(this.cartList.indexOf(item), 1);
			this.selectedList.splice(this.selectedList.indexOf(item), 0)

			this.oldIndex = null;
			this.theIndex = null;

			this.sum();
		},
		// 合计
		sum() { // 合计
			this.sumPrice = 0;
			this.cartList.forEach((item, i) => {
				if (item.selected) {
					this.sumPrice = this.sumPrice + (item.number * item.price)
				}
			})
			this.sumPrice = this.sumPrice.toFixed(2);
		},

		// 全选
		handleSeletedAll() {
			this.isAllselected = !this.isAllselected;
			let arr = [];
			this.cartList.forEach((item, index0) => {
				item.selected = this.isAllselected;
				arr.push(item);
			})

			this.selectedList = this.isAllselected ? arr : [];
			this.sum()

		},
		// 单选框
		handleCheckbox(item) {
			// console.log(item)
			item.selected = !item.selected
			// 判断是否包含改元素
			let flag = this.selectedList.indexOf(item);
			if (flag > -1) {
				this.selectedList.splice(flag, 1)
			} else {
				//push 数组
				this.selectedList.push(item);
			}

			// 全选状态
			if (this.selectedList.length == this.cartList.length) {
				this.isAllselected = true;
			} else {
				this.isAllselected = false;
			}

			this.sum()

		},
		// 点击列表跳详情
		handleGoodsinfo(item) {
			uni.navigateTo({
				url: "../../goods/goods-detail?goodsinfo=" + JSON.stringify(item)
			})
		},
		handleTouchStart(index, event) {
			// console.log(event)
			// 多点触控 不触发
			if (event.touches.length > 1) {
				return;
			}
			this.oldIndex = this.theIndex;
			this.theIndex = null;

			// 初始化坐标
			this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
		},
		//滑动
		handleTouchMove(index, event) {
			// console.log("move")
			if (event.touches.length > 1) {
				return;
			}
			//移动位置
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];

			//滑动位置过下不触发
			//左滑
			if (moveX < 0) {
				this.theIndex = index
			} else {
				this.oldIndex = this.theIndex;
				this.theIndex = null;
			}
		},
		handleTouchEnd(index, event) {
			// console.log("end")
		},

	}
}
